import React, { useEffect, useState } from 'react'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'
import { convertBNBAddressToWBNB, div, numberToWei } from '../../utils/helpers'
import { useCurrentPair } from '../../state/currentPair/hook'
import { useListTokens } from '../../state/token/hook'
import { formatWeiToDisplayNumber } from '../../utils/formatBalance'
import { useWalletTx } from '../../state/wallet/hooks/useWalletTx'
import _ from 'lodash'
import { ethers } from 'ethers'
import moment from 'moment'
import { useTokenPrice } from '../../state/token/hooks/useTokenPrice'
import { useLang } from '../../state/setting/hook'

const bn = ethers.BigNumber.from

export const useWalletTxTable = () => {
  const { walletTxs } = useWalletTx()
  const { tokens } = useListTokens()
  const [data, setData] = useState<any>([])
  const { tokenPrices } = useTokenPrice()
  const { getText } = useLang()

  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()

  useEffect(() => {
    const uniqBy = _.uniqBy(walletTxs, 'transactionHash')
    setData(uniqBy)
  }, [walletTxs])

  const detectInfoToShow = ({
    outputTokenInTx
  }: {
    outputTokenInTx: string
  }) => {
    let inputToken = tokens
      ? tokens[convertBNBAddressToWBNB(inputTokenAddress)]
      : {}
    let outputToken = tokens
      ? tokens[convertBNBAddressToWBNB(outputTokenAddress)]
      : {}

    if (
      STABLECOIN_ADDRESSES.includes(outputTokenAddress.toLowerCase()) &&
      !STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase())
    ) {
      const tmp = { ...inputToken }
      inputToken = { ...outputToken }
      outputToken = tmp
    }

    let isBuy =
      outputTokenInTx.toLowerCase() === outputToken.address.toLowerCase()

    return { inputToken, outputToken, isBuy }
  }

  const column = [
    {
      name: 'outputToken',
      title: getText('order_table.type'),
      className: 'market-order__item hidden-on-phone',
      render: (outputTokenInTx: string) => {
        const { isBuy } = detectInfoToShow({ outputTokenInTx })
        return (
          <span className={`market-order__type ${isBuy ? 'buy' : 'sell'}`}>
            {isBuy ? 'Buy' : 'Sell'}
          </span>
        )
      }
    },
    {
      name: 'outputToken',
      title: getText('order_table.tokens'),
      className: 'market-order__item',
      // @ts-ignore
      render: (outputTokenInTx: string, object: any) => {
        const { isBuy, outputToken } = detectInfoToShow({ outputTokenInTx })
        const amount = isBuy ? object.returnAmount : object.spentAmount

        return (
          <div className={`market-order__token ${isBuy ? 'buy' : 'sell'}`}>
            <p>
              {formatWeiToDisplayNumber(amount, 4, outputToken.decimal || 18)}
            </p>
            <p>{outputToken.symbol}</p>
          </div>
        )
      }
    },
    {
      name: 'spentAmount',
      title: getText('order_table.total_pay'),
      className: 'market-order__item hidden-on-phone',
      // @ts-ignore
      render: (spentAmount: any, object: any) => {
        const { isBuy, inputToken } = detectInfoToShow({
          outputTokenInTx: object.outputToken
        })
        const amount = isBuy ? spentAmount : object.returnAmount
        return (
          <div className={`market-order__total-pay ${isBuy ? 'buy' : 'sell'}`}>
            <p>
              {formatWeiToDisplayNumber(amount, 4, inputToken.decimal || 18)}
            </p>
            <p>{inputToken.symbol}</p>
          </div>
        )
      }
    },
    {
      name: 'price',
      title: getText('order_table.price'),
      className: 'market-order__item',
      //@ts-ignore
      render: (x: string, tx: any) => {
        const { isBuy, outputToken, inputToken } = detectInfoToShow({
          outputTokenInTx: tx.outputToken
        })
        let price = isBuy
          ? div(tx.spentAmount, tx.returnAmount)
          : div(tx.returnAmount, tx.spentAmount)
        const priceDecimal = 18 + outputToken.decimal - inputToken.decimal
        let priceToDisplay = formatWeiToDisplayNumber(
          numberToWei(price, priceDecimal),
          4,
          priceDecimal
        )
        return (
          <div className={`market-order__price ${isBuy ? 'buy' : 'sell'}`}>
            <p>{priceToDisplay}</p>
            <p>
              {STABLECOIN_ADDRESSES.includes(
                outputTokenAddress.toLowerCase()
              ) &&
              !STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase())
                ? outputToken?.symbol
                : inputToken?.symbol}
            </p>
          </div>
        )
      }
    },
    {
      name: 'outputToken',
      title: getText('order_table.current_value'),
      className: 'market-order__item',
      // @ts-ignore
      render: (outputTokenInTx: any, object: any) => {
        const { isBuy, outputToken } = detectInfoToShow({ outputTokenInTx })
        const amount = isBuy ? object.returnAmount : object.spentAmount
        const outputTokenPrice = tokenPrices[outputTokenAddress]
        const currentValue =
          outputTokenPrice && amount
            ? bn(amount).mul(numberToWei(outputTokenPrice))
            : amount
        return (
          <div className='market-order__total-pay'>
            <span className='text-grey'>$</span>
            <span className={isBuy ? 'buy' : 'sell'}>
              {formatWeiToDisplayNumber(
                currentValue.toString(),
                4,
                outputToken.decimal * 2 || 36
              )}
            </span>
          </div>
        )
      }
    },
    {
      name: 'time',
      title: getText('order_table.time'),
      className: 'market-order__item',
      render: (timestamp: number, object: any) => (
        <span className={`market-order__time ${object.type}`}>
          {moment(timestamp).format('DD/MM/YYYY, hh:mm a')}
        </span>
      )
    },
    {
      name: 'transactionHash',
      title: 'Tx',
      className: 'market-order__item text-right market-order__hash',
      render: (hash: string) => (
        <a
          href={'https://bscscan.com/tx/' + hash}
          target='_blank'
          rel='noreferrer'
        >
          {hash.slice(0, 8)}
        </a>
      )
    }
  ]

  return {
    data,
    column
  }
}
