// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'
export const swapEngine = createSlice({
  name: 'swapEngine',
  initialState,
  reducers: {
    setSwapEngine: (
      state,
      action: PayloadAction<{
        swapEngine: any
      }>
    ) => {
      state.swapEngine = action.payload.swapEngine
    },
    setContext: (
      state,
      action: PayloadAction<{
        context: string
      }>
    ) => {
      console.log('set context');
      state.context = action.payload.context
    }
  }
})

// Actions
export const { setSwapEngine, setContext } = swapEngine.actions

export default swapEngine.reducer
