import { useDispatch, useSelector } from 'react-redux'
import { State } from '../../types'
import { setContext, setSwapEngine } from '../reducer'
import { ethers } from 'ethers'
import { useWeb3React } from '../../customWeb3React/hook'
import { ZERO_ADDRESS } from "../../../utils/constant";
import { useSetting } from "../../setting/hook";
import { useSwapInfo } from "../../marketSwap/hooks/useSwapInfo";
// import { StateEngine } from 'swap-engine'
const { SwapEngine } = require('swap-engine')

export const useSwapEngine = () => {
  const { context, swapEngine } = useSelector((state: State) => {
    return {
      context: state.swapEngine.context,
      swapEngine: state.swapEngine.swapEngine
    }
  })

  const { account } = useWeb3React()
  const { referralAddress } = useSetting()
  const { slippage, timeout } = useSwapInfo()

  const dispatch = useDispatch()

  const initSwapEngine = () => {
    const engine = new SwapEngine({

      chain: {
        provider: new ethers.providers.JsonRpcProvider(
          'https://rpc.lz.finance/bsc'
        )
      }
      // debug: true,
    })
    console.log('init engine', engine)

    dispatch(setSwapEngine({ swapEngine: engine }))
  }

  const initContext = () => {
    const context = swapEngine.createSwapContext({
      sender: account || ZERO_ADDRESS,
      referrer: referralAddress,
      slippage: Number(slippage) / 100,
      expiration: Number(timeout) * 60 * 1000
    })

    dispatch(setContext({ context: context }))
  }

  return { context, swapEngine, initSwapEngine, initContext }
}
