import { useDispatch, useSelector } from 'react-redux'
import {
  setBlockConfig as setBlockConfigReduce,
  setLanguage,
  setReferralAddressReduce
} from './reducer'
import { sourceString } from '../../utils/lang'

export const useSetting = () => {
  const { headBlock, timePerBlock, loadedTimePerBlock, referralAddress } =
    useSelector((state: any) => {
      return {
        headBlock: state.setting.headBlock,
        timePerBlock: state.setting.timePerBlock,
        loadedTimePerBlock: state.setting.loadedTimePerBlock,
        referralAddress: state.setting.referralAddress
      }
    })
  const dispatch = useDispatch()

  const setBlockConfig = ({
    headBlock,
    timePerBlock,
    loadedTimePerBlock
  }: {
    headBlock?: number
    timePerBlock?: number
    loadedTimePerBlock?: boolean
  }) => {
    dispatch(
      setBlockConfigReduce({ headBlock, timePerBlock, loadedTimePerBlock })
    )
  }

  const setReferralAddress = (referralAddress: string) => {
    dispatch(setReferralAddressReduce({ referralAddress }))
  }

  return {
    setReferralAddress,
    setBlockConfig,
    headBlock,
    timePerBlock,
    loadedTimePerBlock,
    referralAddress
  }
}

const languageSupported = ['en']

export const useLang = () => {
  const { language } = useSelector((state: any) => {
    return {
      language: state.setting.language
    }
  })
  const dispatch = useDispatch()

  const getText = (key: string) => {
    let result = sourceString[language]
    const arr = key.split('.')
    for (let i = 0; i < arr.length; i++) {
      result = result[arr[i]]
    }
    return typeof result === 'string' ? result : key
    // return sourceString[language][key] ? sourceString[language][key] : key
  }

  const setLang = (lang: string) => {
    if (languageSupported.includes(lang)) {
      dispatch(setLanguage({ language: lang }))
    }
  }

  return {
    setLang,
    getText
  }
}
